$primaryBG: #2a3438;
$seconDaryBG: #352f44;
$activeBG: #5c5470;
$hoverBG: #dbd8e3;

.topnav {
  margin: 5vh 0;
  .right-side-elements {
    input:last-child {
      color: #039608;
    }
  }
}
.sheet {
  &.profile-section {
    background-color: $seconDaryBG;      
    box-shadow: 0px 0px 3px 2px #c1c1c1;  
    margin: 0 0 5vh 0;
    .content {
      display: flex;
      margin-top: 5%;
      align-items: center;
      justify-content: space-around;

      .profile-description {
        color: #ece7e7;
        text-align: center;
        h4 {
          font-family: "Readex Pro", sans-serif;
          font-weight: 100;
          font-size: 2rem;
        }
        h2 {
          font-family: 'Genos', sans-serif;
          font-size: 6rem;          
          color: #64fc3e;
        }
        p {
          font-size: 1.6rem;
          text-align: justify;
          padding: 5vh 6vw;
        }
      }
      .webDevelopementRight {
        display: flex;
        justify-content: flex-end;
        flex-grow: 1;
        img {
          height: 200px;
          width: 200px;
        }
      }
    }
  }

  &.skills {
    background: #ffffff;
    box-shadow: 0px 0px 3px 2px #c1c1c1;
    border-radius: 32px;
    margin: 0 0 5vh 0;
    h3 {
      text-align: center;
    }
    .content {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
      gap: 2rem;
      transition: box-shadow 0.9s linear;
      justify-content: space-around;
      margin: 18vh;
      .card {
        background: $seconDaryBG;
        text-align: center;
        padding: 0;
        margin-bottom: 16px;
        border-color: $seconDaryBG;
        border: unset;
        box-shadow: 0px 0px 3px 2px #c1c1c1;

        .card-header {
          margin: auto;
          flex-direction: row;
          justify-content: stretch;
          background: #fff;
          color: #000;
          border: 1px $seconDaryBG solid;
          border-radius: 8px;
          //border-radius: 7px 7px 0 0;
          transform: translate(0, -50%);
          .cardHeaderText {
            font-size: 16px;
          }
        }
        .card-content {
          display: flex;
          flex-flow: column;
          gap: 1rem;
          padding: 1rem;
          background: $seconDaryBG;
          color: #fff;
          border-radius: 0 0 8px 8px;

          .expertise {
            display: flex;
            flex-flow: row;
            gap: 1rem;
            align-items: center;
            justify-content: center;
          }
          span {
            display: flex;
            background: black;
            width: 30px;
            height: 30px;
            align-items: center;
            justify-content: center;
            border-radius: 16px;
          }
        }
        &:hover {
          box-shadow: 0px 0px 3px 2px #4fc911;
          border-color: #4fc911;
        }
      }
    }
  }

  &.experiences {
    color: white;
    background: $seconDaryBG;
    box-shadow: 0px 0px 3px 2px #c1c1c1;
    //border-radius: 32px;
    margin: 0 0 5vh 0;
    h3 {
      color: #ffffff;
      text-align: center;
    }

    .content {
      margin: 18vh;
      .accordian {
        margin: 16px;
        .accordian-title {
          background: $activeBG;
        }
        .accordian-content {
          color: black;
        }
      }
    }
    .experience {
      height: 9vh;
      background: #ffffff;
      display: flex;
      align-content: center;
      justify-content: space-around;
      margin: 1rem;
      span {
        display: flex;
        align-items: center;
      }
    }
  }
}

#root {
  // background: linear-gradient(
  //   -185deg,
  //   #ffffff 28%,
  //   $primaryBG 28%,
  //   $primaryBG 62%,
  //   #ffffff 62%
  // );
  padding: 0 12vw;
  font-family: 'Nunito', sans-serif;
}

@media screen and (min-width: 601px) and (max-width: 992px) {
  #root {
    padding: 0;
  }

  .sheet {
    &.profile-section {
      .content {
        .profile-description {
          h2 {
            font-size: 5rem;
          }
        }
      }
    }
    
    &.skills {
      .content {
        grid-template-columns: repeat(auto-fit, minmax(40%, 1fr));
        margin: 0;
      }
      h3 {
        margin-bottom: 5vh;
      }
    }
  }
}

/* On screens that are 600px or less, set the background color to olive */
@media screen and (min-width: 200px) and (max-width: 600px) {
  #root {
    padding: 0;
  }

  .topnav.responsive {
    display: block;
    .logoAndMenu {
      display: flex;
      align-items: center;
    }
    .right-side-elements {
      display: block;
      float: none;
    }
  }
  .topnav.responsive div {
    float: none;
    display: block;
    text-align: left;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
  .topnav div:not(:first-child) {
    display: none;
  }
  .topnav .icon {
    position: absolute;
    right: 16px;
    float: right;
    display: block;
  }
  .topnav.responsive input {
    float: none;
    display: block;
    text-align: left;
  }

  .sheet {
    &.profile-section {  
      margin: 0 0 5vh 0;
      .content {
        display: flex;
        margin-top: 5%;
        align-items: center;
        justify-content: space-around;
        .profile-description {
          text-align: center;
          h4 {
            font-weight: 100;
            font-size: 0.8rem;
          }
          h2 {
            font-size: 2.2rem;
          }
          p {
            font-size: 0.8rem;
            text-align: justify;
            padding: 5vh 6vw;
          }
        }
      }
    }
     &.skills {
       h3 {
        margin-bottom: 5vh;
       }
       .content {
        grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
        margin: inherit;
       }
     }
    &.experiences {
      .content {
        margin: 0;
        .accordian {
          .title {
            padding: 8px;
            span {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              font-size: 1rem;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 200px) {
  #root {
    padding: 0;
  }

  .topnav {
    .responsive {
      display: block;

      div {
        float: none;
        display: block;
        text-align: left;
      }
      .logoAndMenu {
        display: flex;
        align-items: center;
      }
      .right-side-elements {
        display: block;
        float: none;
      }
      a {
        float: none;
        display: block;
        text-align: left;
      }
      input {
        float: none;
        display: block;
        text-align: left;
      }
    }
    .icon {
      position: absolute;
      right: 16px;
      float: right;
      display: block;
    }
  }
  .topnav div:not(:first-child) {
    display: none;
  }

  .sheet {
    &.profile-section { 
      .content {
        .profile-description {
          text-align: center;
          h4 {
            font-size: 0.8rem;
          }
          h2 {
            font-size: 2rem;
          }
          p {
            font-size: 0.8rem;
          }
        }
      }
    }
  }
}
